<template>
  <div>
    <h1>{{ Ciudad }}</h1>
    <p>Región: {{ Region }}</p>
    <p>País: {{ Pais }}</p>
    <p>Fecha y Hora: {{ Hora }}</p>
    <p>Latitud: {{ Latitud }}</p>
    <p>Longitud: {{ Longitud }}</p>
    <p>Temperatura: {{ Temperatura }}°C</p>
    <p>Condición: {{ Condicion }}</p>
    <p>Velocidad del viento: {{ VelocidadViento }} km/h</p> 
  </div>
</template>

<script>
export default {
  data() {
    return {
      Ciudad: '',
      Region: '',
      Pais: '',
      Hora: '', 
      Temperatura: 0,
      Condicion: '',
      Latitud: 0,
      Longitud: 0,
      VelocidadViento: 0,
    };
  },
  mounted() {
    this.fetchClimaActual();
  },
  methods: {
    async fetchClimaActual() {
      try {
        const url = 'http://api.weatherapi.com/v1/current.json';
        const apiKey = '8a617536031e41558ac224116232206';
        const ubicacion = 'Trujillo';

        const response = await fetch(`${url}?key=${apiKey}&q=${ubicacion}&lang=es&aqi=yes`);
        const data = await response.json();

        this.Ciudad = data.location.name;
        this.Region = data.location.region;
        this.Pais = data.location.country;
        this.Hora = data.location.localtime; 
        this.Temperatura = data.current.temp_c;
        this.Condicion = data.current.condition.text;
        this.Latitud = data.location.lat;
        this.Longitud = data.location.lon;
        this.VelocidadViento = data.current.wind_kph;
      } catch (error) {
        console.error(error);
      }
    },
  },
};
</script>

